import { RESULTS_FILTERS_HEIGHT } from "@rp/common/settings"
import { styled } from "@rp/react/theme"
import { useDeviceInfo } from "@rp/react/web/common/hooks"

export const StoryCardsHeaderRoot = styled("div", {
  name: "StoryCardsHeaderRoot",
})(({ theme }) => {
  const { isMobile } = useDeviceInfo()

  return {
    width: "100%",
    paddingLeft: isMobile ? theme.spacing(1.5) : 0,
    paddingBlock: theme.spacing(1.5),
    height: isMobile ? "auto" : RESULTS_FILTERS_HEIGHT,

    // Results and Sorters container
    "& > div:last-child": {
      marginTop: theme.spacing(0.5),
      display: "flex",
      justifyContent: "space-between",
      alignItems: "center",
    },
  }
})

export const SortersContainer = styled("div", {
  name: "StoryCardsHeaderSorters",
})(({ theme }) => ({
  display: "flex",
  paddingRight: theme.spacing(1.5),
}))
